import React, {useState} from 'react'
import './Slider.css'
import BtnSlider from './BtnSlider'
import dataSlider from './dataSlider'
import { Col, Row } from "react-bootstrap";
import { CgCPlusPlus } from "react-icons/cg";
import {
    Card,
    CardLeft,
    CardRight,
    Stack,
    BtnGroup,
  } from "../ProjectCard/ProjectCardElements";
  import {
    DiJavascript1,
    DiReact,
    DiNodejs,
    DiMongodb,
    DiPython,
    DiGit,
    DiGithub,
    DiHeroku,
    DiAndroid,
    DiJava,
    DiVisualstudio,
    DiMysql,
    DiPhotoshop,
    DiUnitySmall,
    DiNetbeans,
  } from "react-icons/di";
  import {
    SiPytorch,
    SiFirebase,
    SiNextdotjs,
    SiSelenium,
    SiJava,
    SiAndroidstudio,
    SiSqlite,
    SiCss3,
    SiBlender,
    SiOpencv,
    SiPostman,
    SiJavascript,
    SiPowershell,
    SiXmpp,
    SiCisco,
  } from "react-icons/si";
export default function Sliderchekka() {

    const [slideIndex, setSlideIndex] = useState(1)

    const nextSlide = () => {
        if(slideIndex !== dataSlider.length){
            setSlideIndex(slideIndex + 1)
        } 
        else if (slideIndex === dataSlider.length){
            setSlideIndex(1)
        }
    }

    const prevSlide = () => {
        if(slideIndex !== 1){
            setSlideIndex(slideIndex - 1)
        }
        else if (slideIndex === 1){
            setSlideIndex(dataSlider.length)
        }
    }

    const moveDot = index => {
        setSlideIndex(index)
    }

    return (
        <div className="skill">
        <div>
                <Card>
                <CardLeft>
        <div className="container-slider">
            {dataSlider.map((obj, index) => {
                return (
                    <div
                    key={obj.id}
                    className={slideIndex === index + 1 ? "slide active-anim" : "slide"}
                    >
                        <img 
                        src={process.env.PUBLIC_URL + `/chekka/chekka${index + 1}.jpg`} 
                        />
                    </div>
                    
                )
            })}
            
            <BtnSlider moveSlide={nextSlide} direction={"next"} />
            <BtnSlider moveSlide={prevSlide} direction={"prev"}/>

            <div className="container-dots">
                {Array.from({length: 6}).map((item, index) => (
                    <div 
                    onClick={() => moveDot(index + 1)}
                    className={slideIndex === index + 1 ? "dot active" : "dot"}
                    ></div>
                ))}

               
      
   
            </div>
        </div>
       
    </CardLeft>
        
        <CardRight>

          <h4><b>CHEKKA - ANDROID BASED EXAM CHECKER</b></h4>
          <div className='intro3'>Together with my teammates Mary Antoinette Abuela and Xeniorita Alondra Bio, this is our capstone project. This android app can generate item analysis for teachers, export records to an excel grading report, compute student scores for a specific exam, assess overall class performance, customize answer sheet templates, scan answer sheets for checking, and compute student scores.</div>
         
        
            
            <a
              className="button-50"
              href="https://github.com/Kooper-kosh/Chekka---OpenCV"
              target="_blank"
              rel="noopener noreferrer"
            >
              Source Code
            </a>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
       
       
          <Stack>
            <div className="intro3"><b>Tech Stack</b></div>
            </Stack>
            <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
        
        <Col xs={2} md={.5} className="tech-icons">
          <center>
          <div className="intro2">Android Studio</div>
          </center>
          <DiAndroid />
        </Col>
        <Col xs={2} md={.5} className="tech-icons">
        <center>
          <div className="intro2">SQLite Database</div>
          </center>
          <SiSqlite />
        </Col>
        <Col xs={2} md={.5} className="tech-icons">
        <center>
          <div className="intro2">OpenCV</div>
          </center>
          <SiOpencv />
        </Col>
       
   
      </Row>
   
        
        </CardRight>
      </Card>
 
        </div>
             </div>
    )
}
